import React, { useCallback, useEffect, useState, useMemo } from 'react';
import HistoryContext from './HistoryContext';

function HistoryProvider({ storageKey, children }) {
    const [isReady, setIsReady] = useState(false);
    const [clickHistory, setClickHistory] = useState([]);
    const [searchHistory, setSearchHistory] = useState([]);

    // When mounted fetch data from local storage
    useEffect(() => {
        const results = window.localStorage.getItem(storageKey);
        if (results) {
            const resultsParsed = JSON.parse(results);

            const _clickHistory = resultsParsed.clickHistory || [];
            const _searchHistory = resultsParsed.searchHistory || [];

            setClickHistory(_clickHistory);
            setSearchHistory(_searchHistory);
        }

        setIsReady(true);
    }, []);

    // Clearing local storage.
    const clearHistory = useCallback((type) => {
        const results = window.localStorage.getItem(storageKey);
        if (!results) {
            return;
        }
        const resultsParsed = JSON.parse(results);

        const _clickHistory = type === 'click' ? [] : resultsParsed.clickHistory;
        const _searchHistory = type === 'search' ? [] : resultsParsed.searchHistory;

        // Call service to update storage.
        const history = JSON.stringify({
            clickHistory: _clickHistory,
            searchHistory: _searchHistory,
        });

        window.localStorage.setItem(storageKey, history);

        // Update state with fresh data.
        setClickHistory(_clickHistory);
        setSearchHistory(_searchHistory);
        setIsReady(true);
    }, []);

    const memoValue = useMemo(() => ({
        clickHistory,
        clearHistory,
        searchHistory,
        isReady,
    }));

    return <HistoryContext.Provider value={memoValue}>{children}</HistoryContext.Provider>;
}

export default HistoryProvider;
